.alert-container {
  margin-bottom: 20px;
  position: relative; 
  z-index: 999; 
  width: auto;
}

.MuiAlert-filledSuccess {
  background-color: #4caf50; 
  color: #fff; 
}

.MuiAlert-filledInfo {
  background-color: #2196f3; 
  color: #fff; 
}

.MuiAlert-filledWarning {
  background-color: #ff9800; 
  color: #fff; 
}

.MuiAlert-filledError {
  background-color: #f44336; /* Color de fondo para alertas de error */
  color: #fff; /* Color del texto para alertas de error */
}